'use client'

import { useRouter } from 'next/navigation'

export default function PreferencesPage() {
  const router = useRouter();

  return (
    <div className="min-h-screen bg-gray-100 p-8">
      <div className="max-w-3xl mx-auto bg-white shadow-md rounded-xl p-8">
        <h1 className="text-3xl font-bold text-gray-800 mb-6">Preferences</h1>

        <div className="flex flex-col gap-4">
          <div className="flex justify-between items-center">
            <span className="text-lg font-medium text-gray-700">Dark Mode</span>
            <button className="bg-gray-200 hover:bg-gray-300 rounded-lg px-4 py-2 text-sm font-medium">Enable</button>
          </div>

          <div className="flex justify-between items-center">
            <span className="text-lg font-medium text-gray-700">Email Notifications</span>
            <button className="bg-gray-200 hover:bg-gray-300 rounded-lg px-4 py-2 text-sm font-medium">Manage</button>
          </div>

          <div className="flex justify-between items-center">
            <span className="text-lg font-medium text-gray-700">Language Preference</span>
            <button className="bg-gray-200 hover:bg-gray-300 rounded-lg px-4 py-2 text-sm font-medium">Change</button>
          </div>

          <div className="flex justify-between items-center">
            <span className="text-lg font-medium text-gray-700">Privacy Settings</span>
            <button className="bg-gray-200 hover:bg-gray-300 rounded-lg px-4 py-2 text-sm font-medium">Edit</button>
          </div>
        </div>

        <div className="mt-8">
          <button onClick={() => router.back()} className="px-4 py-2 bg-blue-500 hover:bg-blue-600 text-white rounded-lg">
            Back
          </button>
        </div>
      </div>
    </div>
  )
}
